import React from 'react'
import Helmet from 'react-helmet'
import Layout from '../components/layout'


const Evenement = (props) => (
    <Layout>
        <Helmet>
            <title>Page générique évènement</title>
            <meta name="description" content="Page évènement" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Titre de l'évènement</h1>
                    </header>
                    <p>Description</p>
                    </div>
            </section>
        </div>

    </Layout>
)

export default Evenement